@font-face {
    font-family: 'Open Sans', sans-serif;
    src: url(../src/fonts/Open_Sans/OpenSans-Regular.ttf);
    src: url(../src/fonts/Open_Sans/OpenSans-Bold.ttf);
}

@font-face {
    font-family: 'Lato', sans-serif;
    src: url(../src/fonts/Lato/Lato-Bold.ttf);
    src: url(../src/fonts/Lato/Lato-Light.ttf);
    src: url(../src/fonts/Lato/Lato-Regular.ttf);
}

@font-face {
    font-family: 'Gotham Black';
    src: url(../src/fonts/Gotham_Black/Gotham_Black.otf);
}

@font-face {
    font-family: 'icons';
    src:  url('../src/fonts/icons/icons.eot?kqys6o');
    src:  url('../src/fonts/icons/icons.eot?kqys6o#iefix') format('embedded-opentype'),
      url('../src/fonts/icons/icons.ttf?kqys6o') format('truetype'),
      url('../src/fonts/icons/icons.woff?kqys6o') format('woff'),
      url('../src/fonts/icons/icons.svg?kqys6o#icons') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
  }
  
  [class^="icon__"]::before, [class*=" icon__"]::before {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icons' !important;
    /* speak: never; */
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
  
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  .icon__email:before {
    content: "\e900";
  }
  .icon__facebook:before {
    content: "\e901";
  }
  .icon__linkedin:before {
    content: "\e902";
  }
  .icon__twitter1:before {
    content: "\e904";
  }
  .icon__user:before {
    content: "\e905";
  }
  .icon__phone:before {
    content: "\e942";
  }

