$bg-blue_color: #709dca;
$bg-yellow_color: #ffbb42;
$upper-link: uppercase;
$font-open-sans: "Open Sans", sans-serif;
$font-lato: "Lato", sans-serif;
$font-gotham: "Gotham Black", sans-serif;
$hover-yellow: #ffa915;
$caption-color: #394b50;
$time-hover: all 0.7s;

.how__it-work {
  width: 1170px;
  height: 860px;
  margin: 0 auto;
  padding-top: 155px;
}

.work__caption {
  font-size: 30px;
  font-family: $font-gotham;
  text-transform: $upper-link;
  color: $caption-color;
  text-align: center;
}

.work__paragraf {
  font-family: $font-lato;
  font-size: 18px;
  color: #75888d;
  text-align: center;
  line-height: 22px;
}

.steps {
  width: 100%;
  height: 265px;
  margin-top: 110px;
  text-align: center;
  display: inline-block;
  position: relative;
}

.step {
  width: 289px;
  height: 100%;
  display: inline-block;
}

.step__heading {
  font-size: 20px;
  font-weight: 700;
  font-family: $font-lato;
  color: $caption-color;
  text-transform: $upper-link;
  margin-top: 100px;
}

.step__info {
  font-size: 13px;
  font-family: $font-open-sans;
  color: #7b7b7b;
  display: block;
  line-height: 22px;
  width: 228px;
  margin-left: 22px;
  margin-top: 25px;
}

.arrow__up,
.arrow__up-three,
.arrow__down {
  display: inline-block;
  height: 127px;
  width: 137px;
  transform: rotate(44deg);
  border-top: 2px dotted #709dca;
  border-left: 2px dotted #709dca;
  border-top-left-radius: 139px;
  position: absolute;
  left: 237px;
  top: -57px;
}

.arrow__up::before,
.arrow__up-three::before,
.arrow__down::before {
  content: "";
  display: inline-block;
  width: 1px;
  border: 4px solid transparent;
  border-top: 8px solid $bg-blue_color;
  position: absolute;
  transform: translate(-63px, -6px) rotate(-90deg);
  left: 200px;
}

.arrow__up::after,
.arrow__up-three::after,
.arrow__down::after {
  content: "";
  display: inline-block;
  width: 1px;
  border: 4px solid transparent;
  border-top: 8px solid #709dca;
  position: absolute;
  transform: translate(-205px, 127px) rotate(-3deg);
  left: 200px;
}

.arrow__up-three {
  position: absolute;
  left: 820px;
}

.arrow__down {
  transform: rotate(224deg);
  position: absolute;
  left: 513px;
  top: 12px;
}

.arrow__up:hover::after,
.arrow__up:hover::before,
.arrow__down:hover::before,
.arrow__down:hover::after,
.arrow__up-three:hover::before,
.arrow__up-three:hover::after {
  border: 4px solid transparent;
  border-top: 8px solid #064789;
  transition: $time-hover;
}

.arrow__up:hover,
.arrow__up-three:hover,
.arrow__down:hover {
  border-top: 2px dotted #064789;
  border-left: 2px dotted #064789;
  transition: $time-hover;
}

.step__btn {
  position: relative;
}

.button__step {
  position: absolute;
  left: 0;
  right: 0;
  top: 95px;
  margin: 0 auto;
  min-width: 165px;
  min-height: 47px;
  padding: 6px 15px;
  color: #fff;
  border-radius: 2px;
  text-transform: $upper-link;
  font-size: 14px;
  font-family: $font-lato;
  background-color: $bg-yellow_color;
  cursor: pointer;
  border: none;
  &:hover {
    background-color: $hover-yellow;
    transition: $time-hover;
  }
}
