$bg-blue_color: #709dca;
$bg-yellow_color: #ffbb42;


$white-color: #fff;
$font-open-sans: 'Open Sans', sans-serif;
$font-lato: 'Lato', sans-serif;

$caption-color: #394b50;
$time-hover: all 0.7s;

.footer {
    width: 100%;
    height: 265px;
}

.footer__container {
    width: 1170px;
    border-top: 1px solid #e3e7ec;
    margin: 0 auto;
}

.footer__nav {
    text-align: center;
    &-link {
    font-size: 13px;
    color: $caption-color;
    }
}

.icon__footer  {
    font-size: 23px;
    font-family: $font-lato;
    color:$caption-color;
    text-decoration: none;
    display: inline-block;
}

.footer__logo {
    margin-top: 30px;
 text-align: center;
}

.icon__footer {
    display: inline-block;
}

.icon__call {
    vertical-align: bottom;
}

.footer__link-logo {
    display: block;
    text-align: center;
    margin-top: 25px;
}

.icons__links {
    text-decoration: none;
    margin-left: 15px;
}

.icon__twitter1 {
    color: $bg-yellow_color;
}

.icon__facebook, .icon__linkedin, .icon__phone, .icon__icon_google {
    color: $bg-blue_color;
    text-align: center;
    display: inline-block;
    font-size: 20px;
}

.icon__icon_google {
    font-weight: bold;
}

.icon__linkedin:hover::before,
.icon__twitter1:hover::before,
.icon__facebook:hover::before,
.icon__icon_google:hover::before {
    color: #363636;
    transition: $time-hover;
}

.footer__copy {
    width: 100%;
    height: 45px;
    background-color: $bg-blue_color;
    display: inline-block;
}
.footer__copyright {
    display: block;
    font-family: $font-open-sans;
    font-size: 13px;
    color: $white-color;
    line-height: 24px;
    margin-top: 11px;
    text-align: center;
}

.footer__copy-link {
    text-decoration: none;
    color: $white-color;
    &:hover {
        color: $caption-color;
    }
}