$time-hover: all 0.7s;
$bg-yellow_color: #ffbb42;
$white-color: #fff;
$menu-txt-color: #868686;
$upper-link: uppercase;
$font-lato: 'Lato', sans-serif;


.header {
    width: 1170px;
    min-height: 95px;
    margin: 0 auto;
}

.logo_link {
    float: left;
    margin-bottom: 23px;
}

.nav {
    margin-left: 310px;
    &__list {
    font-size: 0;
    list-style-type: none;
    margin-top: 40px;
    }
    &__item {
    display: inline-block;
    margin-left: 20px;
    // margin-top: 40px;
    }   
    &__link {
    text-decoration: none;
    display: inline-block;
    font-family: $font-lato;
    font-size: 14px;
    text-transform: $upper-link;
    color: $menu-txt-color;
    background-color: $white-color;
    border: 10px solid $white-color;
    }
    &__link:hover {
    background-color: $bg-yellow_color;
    color: $white-color;
    border: 10px solid $bg-yellow_color;
    transition: $time-hover;
    border-radius: 2px;
    }
}