$upper-link: uppercase;
$font-open-sans: "Open Sans", sans-serif;
$font-lato: "Lato", sans-serif;
$font-gotham: "Gotham Black", sans-serif;
$caption-color: #394b50;

.services {
  width: 1170px;
  margin: 200px auto 0;
}

.services__caption {
  text-align: center;
  font-size: 30px;
  font-family: $font-gotham;
  text-transform: $upper-link;
  color: $caption-color;
  line-height: 30px;
}

.services__text {
  text-align: center;
  font-size: 18px;
  font-family: $font-open-sans;
  font-weight: 300;
  color: $caption-color;
  margin-bottom: 95px;
}

.services__box {
  width: 530px;
  float: left;
}

.services__title {
  margin-left: 18px;
  display: inline-block;
  text-transform: $upper-link;
  font-size: 16px;
  font-family: $font-lato;
  color: $caption-color;
}

.title__cap {
  margin-left: 4px;
}

.title__dollar {
  margin-left: 33px;
}

.title__rocket {
  margin-left: 14px;
}

.services__info {
  color: #7b7b7b;
  font-size: 13px;
  font-family: $font-open-sans;
  line-height: 22px;
  margin-left: 84px;
  margin-bottom: 75px;
  margin-top: 20px;
}

.services__info-right {
  margin-left: 30px;
}

.services__img {
  float: left;
  margin-right: 20px;
}

.services__box-two {
  margin-left: 100px;
  display: inline-block;
}
