$upper-link: uppercase;
$menu-txt-color: #868686;
$font-open-sans: "Open Sans", sans-serif;
$font-lato: "Lato", sans-serif;
$font-gotham: "Gotham Black", sans-serif;
$hover-yellow: #ffa915;
$caption-color: #394b50;
$time-hover: all 0.7s;

.newsletter {
  width: 1170px;
  height: 950px;
  margin: 0 auto;
  background-image: url(./img/newsletter-bg.jpg);
  background-repeat: no-repeat;
  background-position: center 340px;
  padding-top: 155px;
}

.news__caption {
  font-size: 30px;
  font-family: $font-gotham;
  color: $caption-color;
  text-align: center;
  text-transform: $upper-link;
}

.news__text {
  color: $caption-color;
  font-size: 16px;
  font-family: $font-open-sans;
  font-weight: 300;
  text-align: center;
  line-height: 22px;
}

input[type="text"].news__input {
  min-width: 310px;
  min-height: 45px;
  padding: 6px 45px;
}

input[type="email"].news__email {
  min-width: 390px;
  min-height: 45px;
  padding: 6px 45px;
}

input[type="submit"].subscribe {
  min-width: 220px;
  min-height: 45px;
  padding: 10px 20px;
}

.news__form {
  margin-left: 117px;
  margin-top: 40px;
}

.news__round {
  width: 280px;
  height: 280px;
  border-radius: 50%;
  background-color: $caption-color;
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  margin-top: 95px;
  &:hover {
    background-color: #3f636d;
    transition: $time-hover;
  }
}

.news__round::after {
  content: "";
  display: inline-block;
  width: 1px;
  height: 20px;
  border: 9px solid transparent;
  border-top: 13px solid #394b50;
  transform: translate(128px, 77px);
}

.news__round:hover::after {
  border: 9px solid transparent;
  border-top: 13px solid #3f636d;
  transition: $time-hover;
}

.address_card {
  text-transform: $upper-link;
  font-size: 15px;
  font-family: $font-lato;
  text-align: center;
  color: #fff;
}

.round__street {
  font-size: 13px;
  font-family: $font-open-sans;
  color: #aeb7ba;
  text-align: center;
  line-height: 24px;
  display: block;
  text-decoration: none;
}

.round__number:hover {
  color: $menu-txt-color;
}

.round__link {
  color: #ffbb42;
  text-decoration: none;
  &:hover {
    color: $hover-yellow;
  }
}

.round__info {
  margin-top: 65px;
}

.logo__news {
  position: absolute;
  left: 110px;
  top: 296px;
  border-bottom: 1px solid #e3e7ec;
}
