$bg-blue_color: #709dca;
$bg-yellow_color: #ffbb42;
$upper-link: uppercase;
$menu-txt-color: #868686;
$white-color: #fff;
$font-open-sans: "Open Sans", sans-serif;
$font-gotham: "Gotham Black", sans-serif;
$hover-yellow: #ffa915;
$time-hover: all 0.7s;

.main {
  width: 100%;
  min-height: 590px;
  background-color: $bg-blue_color;
}

.main__container {
  width: 1170px;
  margin: 0 auto;
}

.main__caption {
  padding-top: 108px;
  text-align: center;
  color: $white-color;
  font-size: 35px;
  font-family: "Gotham-Black", sans-serif;
  text-transform: $upper-link;
  font-weight: bold;
  font-family: $font-gotham;
  line-height: 35px;
  margin: 0;
}

.main__caption-text {
  text-align: center;
  font-size: 16.5px;
  color: $white-color;
  font-family: $font-open-sans;
  font-weight: 300;
  display: block;
}

.main_form {
  margin-top: 32px;
}

input[type="text"],
input[type="email"],
input[type="tel"] {
  min-width: 280px;
  min-height: 45px;
  font-size: 13px;
  font-family: $font-open-sans;
  font-weight: 400;
  text-transform: capitalize;
  padding: 6px 45px;
  //   padding-left: 45px;
  margin-right: 10px;
  &:hover::placeholder {
    color: #363636;
    transition: $time-hover;
  }
}

.submit {
  min-width: 215px;
  min-height: 45px;
  padding: 6px 45px;
  background-color: $bg-yellow_color;
  border: none;
  font-size: 14px;
  font-family: $font-open-sans;
  font-weight: 400;
  border-radius: 2px;
  color: #fff;
  text-transform: $upper-link;
  cursor: pointer;
  &:hover {
    border: 1px solid #fff;
    background-color: #ffa915;
    transition: $time-hover;
  }
}

.icon__user::before,
.icon__email::before,
.icon__phone::before {
  display: inline-block;
  position: absolute;
  border: none;
  width: 14px;
  height: 15px;
  padding: 14px 0 0 15px;
  color: $menu-txt-color;
}

.icon__user:hover::before,
.icon__email:hover::before,
.icon__phone:hover::before {
  color: #363636;
  transition: $time-hover;
}

.icon__user,
.icon__email,
.icon__phone {
  display: inline-block;
}

.macbook {
  display: inline-block;
  margin-top: 70px;
  &__picture {
    width: 650px;
    height: 345px;
    background-image: url(./img/macbook.png);
    background-repeat: no-repeat;
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
  }
  &__screen {
    width: 450px;
    height: 270px;
    background-image: url(./img/macscreen.png);
    background-repeat: no-repeat;
    position: absolute;
    left: 7px;
    right: 0;
    top: 19px;
    margin: 0 auto;
  }
}

.btn {
  margin-left: 173px;
  margin-top: 350px;
  &__three {
    border: 2px solid #dedfe0;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    margin-left: 10px;
    background-color: #fff;
    display: inline-block;
    cursor: pointer;
  }
  &__three:hover {
    border: 2px solid #1ca8dd;
    transition: $time-hover;
  }
}
