
$bg-yellow_color: #ffbb42;
$upper-link: uppercase;
$white-color: #fff;
$font-open-sans: 'Open Sans', sans-serif;
$font-lato: 'Lato', sans-serif;



.client__section {
    width: 100%;
    height: 910px;
    background-color: $bg-yellow_color;
    font-size: 0;
}

.client {
    width: 1170px;
    margin: 0 auto;
}

.client__heading {
    text-align: center;
    padding-top: 140px;
    padding-bottom: 60px;
}   

.client__caption {
    font-size: 30px;
    font-family: $font-open-sans;
    color: $white-color;
    text-transform: $upper-link;
}

.client__text {
    color: $white-color;
    font-size: 16px;
    font-family: $font-open-sans;
}

.client__box {
    width: 570px;
    height: 245px;
    background-color: #fff;
    display: inline-block;
    position: relative;
    font-size: 0;
    border-radius: 1%;
    margin-bottom: 20px;
    position: relative;
}

.client__box2 {
    margin-left: 30px;
}

.client__box1 {
    width: 570px;
    height: 245px;
    background-color: #fff;
    display: inline-block;
    position: relative;
    font-size: 0;
    border-radius: 1%;
}

.client__foto {
    margin-left: 30px;
    margin-top: 43px;
}

.client__info {
    width: 315px;
    // height: 100px;
    font-size: 14px;
    color: #7b7b7b;
    position: absolute;
    top: 50px;
    right: 43px;
    line-height: 23px;
}

.client__info::before {
    content: '“';
    position: absolute;
    left: -17px;
    font-family: $font-lato;
    font-size: 30px;
}

.client__info::after {
    content: '“';
    position: absolute;
    left: 180px;
    bottom: -22px;
    font-family: $font-lato;
    font-size: 30px;
}

.client__link {
    font-size: 14px;
    font-family:$font-lato;
    text-decoration: none;
    color: #f6772c;
    text-transform: $upper-link;
    padding-top: 19px;
    text-align: center;
    display: block;
    padding-left: 25px;
}
.client__link-two {
    padding-left: 123px;
}

.client__link-one {
    padding-left: 100px;
}
.client__link-three {
    padding-left: 55px;
}

.client__status {
    color: #a1b0b4;
    font-size: 14px;
    font-family: $font-lato;
}
