$bg-blue_color: #709dca;
$bg-yellow_color: #ffbb42;
$upper-link: uppercase;
$white-color: #fff;
$font-lato: "Lato", sans-serif;
$font-gotham: "Gotham Black", sans-serif;
$hover-yellow: #ffa915;
$caption-color: #394b50;
$time-hover: all 0.7s;

.price__bg {
  width: 100%;
  height: 900px;
  background-color: #e7edf3;
}

.price__box {
  padding-top: 135px;
  width: 1170px;
  margin: 0 auto;
  height: 100%;
}

.price__caption {
  font-size: 30px;
  font-family: $font-gotham;
  text-transform: $upper-link;
  color: $caption-color;
  text-align: center;
}

.price__text {
  font-family: $font-lato;
  font-size: 18px;
  color: #75888d;
  text-align: center;
  line-height: 22px;
}

.price__card {
  width: 1170px;
  margin: 0 auto;
}

.price__cards {
  width: 100%;
  height: 440px;
  margin-top: 50px;
  font-size: 0px;
  text-transform: uppercase;
}

.price__card {
  width: 100%;
  height: 440px;
  margin-top: 50px;
  font-size: 0px;
  text-transform: uppercase;
}

.price__card:nth-child(2) {
  width: 395px;
  height: 500px;
  background-color: $bg-blue_color;
  position: relative;
  top: 0px;
  padding-top: 88px;
  border: none;
}

.price__card {
  display: inline-block;
  width: 387px;
  height: 440px;
  padding: 53px 30px 0 30px;
  color: $caption-color;
  text-align: center;
  background-color: #fff;
  border: 1px solid #ccd5df;
  border-radius: 3px;
  font-size: 14px;
}

.price__title {
  font-family: $font-lato;
  font-size: 25px;
  font-weight: 300;
}

.price__dollar {
  color: $bg-yellow_color;
  display: inline-block;
  font-family: "Lato", sans-serif;
  font-weight: 700;
  font-size: 60px;
  margin-top: 28px;
}

.price__for-month {
  font-family: $font-lato;
  font-weight: 300;
  font-size: 20px;
  display: block;
  color: $caption-color;
}

.price__premium {
  color: $white-color;
}

.price__info {
  font-weight: 400;
  color: $caption-color;
  font-size: 14px;
  font-family: $font-lato;
  margin-top: 30px;
}

.price__info-text {
  display: block;
  color: #b5bbc1;
  border-top: 1px solid #e2e9f0;
  padding: 10px 0;
}

.price__premium-2 {
  color: #bdddfc;
  border-top: 1px solid #8eb8e2;
}

.price__btn {
  min-width: 130px;
  min-height: 42px;
  padding: 6px 15px;
  font-size: 0;
  background-color: $bg-yellow_color;
  margin-top: 23px;
  border: none;
  border-radius: 2px;
  text-transform: $upper-link;
  font-size: 14px;
  font-family: $font-lato;
  color: #fff;
}

.price__btn:hover {
  background-color: $hover-yellow;
  transition: $time-hover;
}

.price__card:hover {
  background-color: #f8f8f8;
  transition: $time-hover;
}

.price__card-two:hover {
  background-color: #558ec7;
  transition: $time-hover;
}
