$bg-yellow_color: #ffbb42;
$upper-link: uppercase;
$white-color: #fff;
$font-open-sans: "Open Sans", sans-serif;
$font-lato: "Lato", sans-serif;
$font-gotham: "Gotham Black", sans-serif;
$hover-yellow: #ffa915;
$time-hover: all 0.7s;

.section {
  width: 100%;
  background-color: $bg-yellow_color;
  margin-top: 155px;
  font-size: 0;
}

.section__about {
  width: 1425px;
  margin: 0 auto;
}

.section__about-us {
  width: 50%;
  float: left;
}

.section__about-img {
  width: 100%;
  display: inline-block;
}
.section__img-about {
  width: 100%;
}

.section__about-text {
  width: 50%;
  float: left;
  margin-top: 115px;
  position: relative;
}

.section__heading {
  font-size: 30px;
  font-family: $font-gotham;
  color: $white-color;
  text-transform: $upper-link;
  margin-bottom: 25px;
  padding-left: 80px;
}

.section__about-info {
  font-size: 13px;
  color: $white-color;
  font-family: $font-open-sans;
  line-height: 24px;
  height: 155px;
  width: 585px;
  padding-left: 80px;
}

.section__btn-about {
  min-width: 145px;
  min-height: 47px;
  padding: 6px 15px;
  margin-left: 80px;
  position: absolute;
  top: 240px;
  left: 0;
  font-size: 14px;
  font-family: $font-lato;
  text-transform: $upper-link;
  color: $white-color;
  background-color: $bg-yellow_color;
  border: 1px solid #e7ad48;
  cursor: pointer;
  &:hover {
    background-color: $hover-yellow;
    border: 1px solid #fff;
    transition: $time-hover;
  }
}
